body {
  background-color: #9b59b6;
}

canvas {
  display: block;
  height: auto;
  margin: 0;
  display: block;
}

.loading {
  font-weight: 900;
  font-size: calc(2rem + 5vmin);
  line-height: calc((2rem + 5vmin) * 0.8);
  padding-top: 0.2rem;
  position: absolute;
  top: 0;
  left: 0;
  text-align: left;
  margin: 3rem;
  pointer-events: none;
}

.-gyon {
  background-color: #9b59b6;
  color: #9b59b6;
}

.-thunder {
  background-color: #e67e22;
  color: #e67e22;
}

.-drone {
  background-color: #e74c3c;
  color: #e74c3c;
}

.swiper-slide {
  display: flex;
}

.canvas-container {
  margin: 0 auto;
  display: block;
}

.note {
  position: fixed;
  left: 0;
  width: 100%;
  text-align: center;
  z-index: 10;
  line-height: 32px;
  font-size: 17px;
  font-weight: 500;
  color: #000;
}

.note.-more {
  bottom: 0;
}

.note.-how {
  top: 0;
}
